import React,{Component} from 'react';
import axios from 'axios';
import Button from '@material-ui/core/Button';
import FileBase64 from 'react-file-base64';
import {reactLocalStorage} from 'reactjs-localstorage';


class CategoryUpload extends Component{
    constructor(props){
        super(props);
        this.state = {
            files: "",
            id : this.props.idcategory
        }
    }
    getFiles(files){
        this.setState({ files: files.base64 });
        console.log(files.base64);
    }
    submitHandler = (event) => {
        const payload = {
            "file" : this.state.files,
            "id": this.state.id
        }
        let token= reactLocalStorage.get('auth');
        const AuthStr = "Bearer ".concat(token); 
        axios.post('http://13.229.249.101/dashboard/api/category/upload/new', payload, { headers: { Authorization: AuthStr } })
        // axios.post('http://localhost:8000/category/upload/new', payload, { headers: { Authorization: AuthStr } })
        .then((response) => {
            console.log(response.statusText);
            if(response.statusText === 'OK'){
                window.location.reload();
            }
        })
        .catch((error) => {
            console.log(error);
        });  
        event.preventDefault(); 
    }

    componentDidUpdate(prevProps){
        if(prevProps.idcategory !== this.props.idcategory){
           this.setState({
               id : this.props.idcategory
           })
        }      
     }
    render(){
        return(
            <div className="box-default m-3">
                <div className="box-body py-5">
                    <div className="form-group">
                        <h6>Upload Image Category</h6>
                    </div>
                    <form  onSubmit={this.submitHandler}>
                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Upload Image</label>
                        <div className="col-md-10">
                        <FileBase64
                        multiple={ false }
                        onDone={ this.getFiles.bind(this) } />
                        </div>
                    </div>
                    <div className="form-group row">
                        <div className="col-md-2"></div>
                        <div className="col-md-10">
                        <Button variant="contained" className="btn-w-md" type="submit"> Upload </Button>
                        </div>
                    </div>
                    </form>
                </div>
            </div>
        );
    }
}

export default CategoryUpload; 